import {NotificationBuilder} from "../../ui/notifications";
import lang from "../../lang";

import laroute from "/js/laroute.js";

export default function (event) {

    let builder:NotificationBuilder = new NotificationBuilder();
    builder
        .setText(lang.get('notification.cure.need_review'))
        .addAction()
            .setText(lang.get('notification.cure.action.open'))
            .setUrl(laroute.route('cure.show', {cure: event.cure.id}))
            .stop()
        .notify();

}
